body {
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: #fbf8ef;
  font-family: sans-serif;
  font-size: 24px;
  font-weight: 900;
}

.field-cell {
  background: #d6cdc4;
  width: 50px;
  height: 50px;
  color: #776e65;
  box-sizing: border-box;
  text-align: center;
  vertical-align: center;
  cursor: pointer;
}

.game-field {
  border-spacing: 3px;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
